import React, {useState} from 'react';
import Minusbtn from '../icons/minus-icon.svg';
import Plusbtn from '../icons/plus-bold.svg';
import MultiRangeSlider from "multi-range-slider-react";
import PaginatedItems from './Pagination'
import Footer from './Footer';

import "./selectall.css"
import './card.css';
import { CheckboxGroup, AllCheckerCheckbox, Checkbox } from "@createnl/grouped-checkboxes";


export default function Cardlist() {
  const [minus, setMinus] = useState(false);
  const [Plus, setPlus] = useState(false);

  const [minValue, set_minValue] = useState(0);
  const [maxValue, set_maxValue] = useState(75);
  const handleInput = (e) => {
    set_minValue(e.minValue);
    set_maxValue(e.maxValue);
  };
// min max scrolling
 return <>
  <div className="container">

    <div className="product_list">
      <div className="side_navbar">
        <p>Filters</p>
        <div className="price_min_max pricing">
          <div className="price_content">
            <h3>Price</h3>
            <button>Clear all</button>
          </div>
          <MultiRangeSlider
            min={0}
            max={100}
            step={5}
            ruler={true}
            label={true}
            preventWheel={false}
            minValue={minValue}
            maxValue={maxValue}
            onInput={(e) => {
              handleInput(e);
          }}/>
            <div className="min_max">
              <button className='min'>Min</button>
              <button className='max'>Max</button>
            </div>
        </div>
        {/* onChange={setOnChange} */}
        <CheckboxGroup >
        <div className="price_min_max">
          <div className="price_mm_content">
          <h3>Select All</h3>
          <label className="chackbox_container selectAll">
            <AllCheckerCheckbox className="group" onClick={()=>{setMinus(true); setPlus(true)} } />
            <span className="checkmark"></span>
          </label>
          </div>
        </div>
        <div className="price_min_max categorys" >

          <div className="price_mm_content" onClick={(e)=>setMinus(!minus)}>
          <h3>Category</h3>
          <img className='minus-btn' src={minus? Minusbtn : Plusbtn} alt="Minusbtn" />
          </div>
          <div className={minus? "content_options" : "content_options hide" }>
          <label className="chackbox_container">Option 1
            <Checkbox name="Option 1"  />
            <span className="checkmark"></span>
          </label>  
          <label className="chackbox_container"> Option 2 
            <Checkbox name="Option 2" />
            <span className="checkmark"></span>
          </label>
          <label className="chackbox_container"> Option 3 
            <Checkbox name="Option 3" />
            <span className="checkmark"></span>
          </label>
          <label className="chackbox_container"> Option 4 
            <Checkbox name="Option 4" />
            <span className="checkmark"></span>
          </label>
          </div>
          {/* </CheckboxGroup> */}
        </div>
        <div className="price_min_max categorys" >
          <div className="price_mm_content" onClick={(e)=>setPlus(!Plus)}>
            <h3 >Category</h3>
            <img className='Plus_btn' src={Plus? Minusbtn : Plusbtn} alt=" Plus btn" />
          </div>
          <div className={Plus? "content_options" : "content_options hide" }>
          <label className="chackbox_container">Option 1
            <Checkbox name="Option 1" />
            <span className="checkmark"></span>
          </label>  
          <label className="chackbox_container"> Option 2 
            <Checkbox name="Option 2" />
            <span className="checkmark"></span>
          </label>
          <label className="chackbox_container"> Option 3 
            <Checkbox name="Option 3" />
            <span className="checkmark"></span>
          </label>
          <label className="chackbox_container"> Option 4 
            <Checkbox name="Option 4" />
            <span className="checkmark"></span>
          </label>
          </div>
        </div>
        </CheckboxGroup>
      </div>
      <div className="card_list_content">
        <PaginatedItems/>
      </div>
    </div>
    </div>
    <Footer/>
  </>;
}   
