import React from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
// import required modules
import { Pagination,Navigation } from "swiper";
import ImgIcon from '../icons/img-icon.svg';
import Avatar from '../icons/Avatar.svg';



// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";

import "./Home.css";
import CardsSlider from "./CardsSlider";
import Footer from "./Footer";
import BlackButton from "./Buttons/BlackButton";
export default function Home() {
  return <>
  <div className="container">
      <div className="home_section">
      <div className="home_slider">
        <Swiper 
        pagination={{
            clickable: true,
            }} 
        modules={[Pagination]}  
        className="mySwiper">
            <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
            <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
            <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
            <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
            <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
            <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
            <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
            <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
            <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
        </Swiper>
      </div>
      <div className="feature_product">
        <h2 className="section_heading">Related Products</h2>
        <CardsSlider/>
        <BlackButton>EXPLORE</BlackButton>
      </div>
      <div className="top_categories_section">
        <h2 className="section_heading">Top Categories</h2>
        <div className="top_Categories_content">
            <div className="category">
                <div className="category_img">
                    <img src={ImgIcon} alt="ImgIcon" />
                </div>
                <div className="category_name">
                    <h3>Category Name</h3>
                </div>
            </div>
            <div className="category">
                <div className="category_img">
                    <img src={ImgIcon} alt="ImgIcon" />
                </div>
                <div className="category_name">
                    <h3>Category Name</h3>
                </div>
            </div>
        </div>
        <BlackButton>View all Categories</BlackButton>
      </div>
      <div className="Latest_product">
        <h2 className="section_heading">latest Products</h2>
        <CardsSlider/>
        <BlackButton>EXPLORE</BlackButton>

      </div>
      <div className="testimonial_section">
        <h2 className="section_heading">Testimonials</h2>
            <div className="tastimonial_slider">
            <Swiper navigation={true} modules={[Pagination,Navigation]} pagination={{
          clickable: true,
        }} className="mySwiper">
                <SwiperSlide><div className="testimonial_content">
                        <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliquat enim ad minim veniam.</p>
                        <img className="user_img" src={Avatar} alt="Avatar" />
                        <h3>Sean Farmer</h3>
                        <h4>Designation</h4>
                    </div></SwiperSlide>
                <SwiperSlide><div className="testimonial_content">
                        <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliquat enim ad minim veniam.</p>
                        <img className="user_img" src={Avatar} alt="Avatar" />
                        <h3>Sean Farmer</h3>
                        <h4>Designation</h4>
                    </div></SwiperSlide>
                <SwiperSlide><div className="testimonial_content">
                        <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliquat enim ad minim veniam.</p>
                        <img className="user_img" src={Avatar} alt="Avatar" />
                        <h3>Sean Farmer</h3>
                        <h4>Designation</h4>
                    </div></SwiperSlide>
            </Swiper>
        </div>
      </div>
      </div>
  </div>
  <Footer/>

</>
}

