import React from 'react';
import { Link } from 'react-router-dom';

import '../Navbar.css';
import Usericon from '../../icons/icon_User.svg';
import ArrowDown from '../../icons/small-down-arrow.svg'
import ShopIcon from '../../icons/icon-Shop.svg'




function User({showContent, setShowContent}) {

    return (
        <div className='user'>
        <div className="cart" onClick={()=>setShowContent(!showContent)}>
            <img src={ShopIcon} alt='User icon'/>
            <div className={showContent? "cart_total": "cart_total cart_hide "}>
                <h4>0</h4>
            </div>
        </div>
        <img className='user_icon' src={Usericon} alt='User icon'/>
        <p>Hello, User <img className='down_arrow' src={ArrowDown} alt='ArrowDown'/>
        <span className="user-content">
            <Link className="dropbtn" to="#">My Profile</Link>
            <Link to="/wish-list">Wishlist</Link>
            <Link to="/my-orders">Orders</Link>
            <Link to="#">Sign Out</Link>
        </span>
        </p>
    </div>
    );
  }
  
  export default User;
