import React from 'react';
import { Link } from 'react-router-dom';
import FavIcon from '../../icons/favorite_icon.svg';
import Imgicon from '../../icons/img-icon.svg';
import ShoppingCart from '../../icons/shopping-cart-2.svg';


export default function Card({title,index}) {
  return <>
        <div className="card">

          <div className="card_icon">
            <Link to="#">
              <img src={FavIcon} alt="thumb" />
            </Link>
          </div>
          <Link to="/product">
              <div className="card_img">
                <img src={Imgicon} alt="thumb" />
              </div>
          </Link>
          <div className="card_footer">
          <div className="product_content">
            <h4>{title} {index}</h4>
            <p>product price</p>
          </div>
          <img src={ShoppingCart} alt=" ShoppingCart" />
          </div>
        </div>
  </>;
}
