import React from 'react';
import { Link } from 'react-router-dom';
import '../Navbar.css';
import ArrowDown from '../../icons/arrow-down.svg'
import RightArrow from '../../icons/right-arrow.svg'


function Logo(props) {
    const {show} = props;
    return (
        <ul className={show? "hide": " "}>
            <li><Link to="/products">View Shop</Link></li>
            <li>
                <div className="dropdown">
                    <button className="dropbtn">Categories <img src={ArrowDown} alt='ArrowDown'/></button>
                    <div className="dropdown-content">
                        <div className="sub_dropbtn"><Link to="#">Category 2  <img src={RightArrow} alt="RightArrow" /></Link>
                            <div className="dropdown_sub_content">
                            <Link to="#">SUB-Category 1 </Link>
                            <Link to="#">SUB-Category 2 </Link>
                            </div>
                        </div>
                        <Link to="#">Category 2  <img src={RightArrow} alt="RightArrow" /></Link>
                        <Link to="#">Category 3  <img src={RightArrow} alt="RightArrow" /></Link>
                    </div>
                </div>
            </li>
            <li><Link to="/Offers">Offers</Link></li>
            </ul>

    );
  }
  
  export default Logo;
  