import React from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
// import required modules
import { Pagination } from "swiper";
import ImgIcon from '../icons/img-icon.svg';



// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";

import "./Home.css";
import CardsSlider from "./CardsSlider";
import Footer from "./Footer";
import BlackButton from "./Buttons/BlackButton";
export default function Offers() {
  return <>
  <div className="container">
    <div className="offer_section">
      <div className="home_slider">
          <Swiper pagination={true} modules={[Pagination]} className="mySwiper">
              <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
              <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
              <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
              <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
              <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
              <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
              <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
              <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
              <SwiperSlide><img src={ImgIcon} alt="ImgIcon" /></SwiperSlide>
          </Swiper>
        </div>
        <div className="feature_product">
          <h2 className="section_heading">Summer Offer</h2>
          <CardsSlider/>
          <BlackButton>EXPLORE</BlackButton>
        </div>
        <div className="Latest_product">
          <h2 className="section_heading">Winter Offer</h2>
          <CardsSlider/>
          <BlackButton>EXPLORE</BlackButton>
        </div>
    </div>
      
  </div>
  <Footer/>

</>
}

