import React from 'react';
import '../Navbar.css';
import {Link} from 'react-router-dom'
import Navicon from '../../icons/nav-icon.svg';


function Logo() {
    return (
        <div className='logo'>
            <Link to="/"><img src={Navicon} alt='nav-icon'/></Link>
        </div>
    );
  }
  
  export default Logo;
  