import React, { useState } from "react";
import Footer from './Footer';

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import Fevorite from '../icons/Favorite.svg';





// Import Swiper styles
import "swiper/css";
import "swiper/css/free-mode";
import "swiper/css/navigation";
import "swiper/css/thumbs";
import "swiper/css/pagination";


import "./ProductDetails.css";

// import required modules
import { FreeMode, Navigation, Thumbs } from "swiper";
import LargeImg from "../icons/large-img.png";
import SmallImg from "../icons/small-img.png";
import CardsSlider from "./CardsSlider";
import BlackButton from "./Buttons/BlackButton";
import Quantity from "./Quantity";

export default function App() {
  const [thumbsSwiper, setThumbsSwiper] = useState(null);

  return (
    <>
    <div className="container">
        <div className="PD_contents">
        <div className="product_slider">
            <Swiper
                style={{
                "--swiper-navigation-color": "#fff",
                "--swiper-pagination-color": "#fff",
                }}
                spaceBetween={10}
                direction={"vertical"}
                navigation={false}
                thumbs={{ swiper: thumbsSwiper }}
                mousewheel={true}
                pagination={{
                    clickable: true,
                }}
                modules={[FreeMode, Navigation, Thumbs]}
                className="mySwiper2"
            >
                <SwiperSlide>
                <img src={LargeImg} alt="thumb"/>
                </SwiperSlide>
                <SwiperSlide>
                <img src={LargeImg} alt="thumb"/>
                </SwiperSlide>
                <SwiperSlide>
                <img src={LargeImg} alt="thumb"/>
                </SwiperSlide>
                <SwiperSlide>
                <img src={LargeImg} alt="thumb"/>
                </SwiperSlide>
                <SwiperSlide>
                <img src={LargeImg} alt="thumb"/>
                </SwiperSlide>
            </Swiper>
            <Swiper
                onSwiper={setThumbsSwiper}
                spaceBetween={13}
                slidesPerView={4}
                freeMode={true}
                direction={"vertical"}
                watchSlidesProgress={true}
                modules={[FreeMode, Navigation, Thumbs]}
                className="mySwiper"
            >
                <SwiperSlide>
                <img src={SmallImg} alt="thumb" />
                </SwiperSlide>
                <SwiperSlide>
                <img src={SmallImg} alt="thumb" />
                </SwiperSlide>
                <SwiperSlide>
                <img src={SmallImg} alt="thumb" />
                </SwiperSlide>
                <SwiperSlide>
                <img src={SmallImg} alt="thumb" />
                </SwiperSlide>
                <SwiperSlide>
                <img src={SmallImg} alt="thumb" />
                </SwiperSlide>
            </Swiper>
        </div>
        <div className="PD_text_content">
            <div className="product_name">
                <h3>Product Name</h3>
                <p className="path">Category , Sub- Category</p>
            </div>
            <div className="product_price">
                <h3>৳420.14</h3>
                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat incididunt ut labore et dolore.</p>
            </div>
            <Quantity/>
            <div className="add_to">
                <BlackButton><p>ADD TO CART</p> </BlackButton>
                <button className="add_wish"><img src={Fevorite} alt="Fevorite" /> Add to wishlist</button>
            </div>
    </div>
        </div>
        
        <div className="related_products">
            <h2>Related Products</h2>
            <CardsSlider/>
        </div>
        
    
    </div>
    <Footer/>
    
     
    </>
  );
}
