import React,{useState} from 'react';

import './Navbar.css';
import Logo from './navcomponents/logo';
import Lists from './navcomponents/lists';
import Search from './navcomponents/searchfild';
import User from './navcomponents/user';


function NavBar({showContent, setShowContent}) {
   const [show, setShow] = useState(false);
  
  function handlesearch (e){
      e.preventDefault()
    setShow(!show)
  }
   
    return (
      <nav>
        <div className="navbar">
        <div className='nav_links'>
            <Logo/>
            <Lists show= {show}/>
          </div>
          <div className='nav_search'>
            <Search show= {show} handlesearch = {handlesearch}/>
            <User showContent= {showContent} setShowContent= {setShowContent} />
          </div>
        </div>
       </nav>
    );
  }
  
  export default NavBar;
  