import React,{useState} from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import Cardlist from './components/cardlist'
import NavBar from './components/Navbar';
import ProductDetails from './components/ProductDetails';
import Home from './components/Home';
import Cart from "./components/navcomponents/Cart";


import SignIn from './components/pages/SignIn';
import DeliveryAddressEdit from './components/pages/DeliveryAddressEdit';
import ProfileEdit from './components/pages/ProfileEdit';
import "./App.css"
import Offers from './components/Offers';
import WishList from './components/WishList';
import MyOrders from './components/MyOrders';

function App() {
  const [showContent, setShowContent] = useState(false);
  

  return (
    <div>
      <NavBar showContent={showContent} setShowContent={setShowContent}/>
      <Routes>
        <Route path="/" element={<Navigate to="/Home"/>}/>
        <Route path="/Home" element={<Home/>}/>
        <Route path="/Offers" element={<Offers/>}/>
        <Route path="/products" element={<Cardlist/>}/>
        <Route path="/product" element={<ProductDetails/>}/>
        <Route path="/wish-list" element={<WishList/>}/>
        <Route path="/my-orders" element={<MyOrders/>}/>
        <Route path="/signin" element={<SignIn/>}/>
        <Route path="/delivery-address-edit" element={<DeliveryAddressEdit/>}/>
        <Route path="/profile-edit" element={<ProfileEdit/>}/>
      </Routes>
      <Cart showContent= {showContent} setShowContent= {setShowContent}/>

    </div>
  );
}

export default App;
